.mini-container {
  display: flex;
  padding: 20px;
  align-items: center;
  gap: 10px;
}
.mini-text-content {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.mini-text-content p {
  color: #3483fa;
  font-size: 14px;
  margin: 0px;
}
.mini-text-content h5 {
  font-size: 16px;
  font-weight: 400;
  margin: 0px;
}
