.nav-centro {
  display: flex;
  font-size: 14px;
  align-items: baseline;
  justify-content: space-between;
  gap: 10px;
  position: relative;
}
.nav-centro p {
  padding: 5px;
  cursor: pointer;
}
.categorias-generales {
  position: absolute;
  z-index: 2;
  list-style-type: none;
  background-color: #333;
  color: whitesmoke;
  border-radius: 5px;
  padding-left: 0px;
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 260px;
  top: 15px;
  left: -110px;
}
