.beneficio-container {
  position: relative;
}
.beneficio-img-container {
  width: 100%;
  height: 250px;
  border-radius: 7px;
  overflow: hidden;
}
.beneficio-img {
  width: 100%;
}
.beneficio-logo-and-text-container {
  display: flex;
  position: absolute;
  bottom: 16px;
  left: 16px;
}
.beneficio-logo-container {
  border-radius: 7px;
  overflow: hidden;
  width: 80px;
}
.beneficio-logo {
  width: 100%;
  height: 100%;
}
.beneficio-text {
  color: #fff;
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-left: 16px;
  justify-content: center;
}
.beneficio-text span:first-child {
  font-size: 22px;
  font-weight: 600;
}
.beneficio-text span:last-child {
  font-size: 18px;
  font-weight: 400;
}
