.second-article {
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 120px;
  padding: 16px;
  margin: 0 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.second-article-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.second-article-title {
  color: #737373;
  font-size: 16px;
  font-weight: 600;
  margin: 0;
}
.second-article-span-blue-text {
  color: #3483fa;
  font-size: 14px;
  font-weight: 400;
}
