.list-item {
  padding: 10px 40px;
  text-align: left;
  font-size: 14px;
  font-weight: 600;
  position: relative;
}
.list-item:hover {
  background-color: #3483fa;
}
.sub-cat-on-hover {
  width: 500px;
  position: absolute;
  background-color: aliceblue;
  right: -499px;
  top: 0px;
}
.sub-cat-department-list {
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.sub-cat-department-list li {
  list-style-type: none;
  color: black;
  margin: 10px;
  font-size: 14px;
}
