.suscribite-article {
  width: 100%;
  background-color: #fff;
  box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  overflow: hidden;
}
.upper {
  width: 100%;
  background: rgb(169, 15, 144);
  background: linear-gradient(90deg, rgba(169, 15, 144, 1) 50%, rgba(12, 26, 81, 1) 100%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px;
}
.suscribite-article-title {
  font-size: 26px;
  font-weight: 600;
  color: #fff;
}
.suscribite-article-prices {
  color: #fff;
  display: flex;
  flex-direction: column;
  border-left: solid;
  padding-left: 10px;
  text-align: left;
}
.suscribite-article-prices p {
  margin: 0;
}
.suscribite-article-prices p:first-child {
  font-size: 16px;
  text-decoration: line-through;
}
.suscribite-article-prices p:last-child {
  font-weight: 600;
  font-size: 28px;
}
.suscribite-article-prices p:last-child span {
  font-size: 20px;
}
.middle h5 {
  font-size: 18px;
  font-weight: 400;
  text-align: left;
  padding: 0 30px;
  margin-bottom: 0px;
}
.middle-beneficts {
  padding: 10px 60px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
}
.benefit {
  display: flex;
  align-items: center;
}
.benefit p {
  max-width: 200px;
  text-align: left;
  font-size: 16px;
}
.down {
  padding: 20px 30px;
  border-top: 1px solid #ededed;
}
.down-suscribe {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100px;
  height: 32px;
  border-radius: 5px;
  margin-left: auto;
  padding: 0 12px;

  color: #fff;
  background-color: #3483fa;
  font-size: 14px;
  font-weight: 600;
}
