.third-article {
  display: flex;
  flex-direction: column;
}
.third-article-title-container {
  margin: 16px 0px;
  border: 1px solid #e6e6e6;
  width: 100%;
  display: flex;
  justify-content: center;
  border-top: none;
}
.third-article-title {
  border: 1px solid #e6e6e6;
  padding: 3px;
  width: 150px;
  margin: 0;
  color: #666;
  font-size: 13px;
  font-weight: 400;
  border-bottom: none;
}
.third-article-options {
  display: flex;
  justify-content: space-between;
}
.third-article-options-span {
  color: #333;
  font-size: 13px;
}
.third-article-copyright {
  text-align: left;
}
.third-article-copyright-p {
  font-size: 12px;
  color: #999;
}
