.ofertas-slider {
  display: flex;
  align-items: center;
}
.slider-container {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.slider-content {
  width: 100%;
  display: flex;
  gap: 20px;
  justify-content: space-between;
}
.navigation-btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;
  width: 64px;
  height: 64px;

  cursor: pointer;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.12);
  z-index: 3;
}
.navigation-btn:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.1);
}
.prev-btn {
  left: 50px;
}
.next-btn {
  right: 50px;
}
.btn-img-prev {
  width: 25px;
  transform: scaleX(-1);
}
.btn-img-next {
  width: 25px;
}
